body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-sf-ui-text: "SF UI Text";
  --font-sf-pro-text: "SF Pro Text";

  /* font sizes */
  --font-size-2xs: 11px;
  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-3xs: 10px;
  --font-size-6xs-3: 6.3px;

  /* Colors */
  --basic-white: #fff;
  --color-gray: rgba(0, 0, 0, 0.4);
  --color-deepskyblue: #3ea9e6;
  --black: #000;
  --color-silver: #c4c4c4;
  --color-dodgerblue: #0098fd;
  --color-darkslategray: #20435b;
  --instagram-gradient: #9e2692;
  --instalive-gradient: #5b00c4;

  /* Gaps */
  --gap-xl: 20px;
  --gap-base: 16px;
  --gap-9xs: 4px;
  --gap-7xs: 6px;
  --gap-91xl: 110px;
  --gap-xs: 12px;
  --gap-6xs: 7px;
  --gap-12xs: 1px;
  --gap-118xl-5: 137.5px;
  --gap-218xl: 237px;
  --gap-xs-9: 11.9px;

  /* Paddings */
  --padding-8xs: 5px;
  --padding-5xs: 8px;
  --padding-9xs: 4px;
  --padding-xl: 20px;
  --padding-mid: 17px;
  --padding-mini: 15px;
  --padding-4xs: 9px;
  --padding-lg: 18px;
  --padding-3xs: 10px;
  --padding-9xs-6: 3.6px;
  --padding-10xs: 3px;
  --padding-lg-9: 18.9px;
  --padding-11xs: 2px;
  --padding-2xs: 11px;
  --padding-7xs: 6px;
  --padding-12xs: 1px;

  /* Border radiuses */
  --br-81xl: 100px;
  --br-13xl-5: 32.5px;
  --br-12xs-9: 0.9px;
}
